* {
	margin: 0;
	box-sizing: border-box;
	font-family: 'MS-500'; }

header {
	background-image: url('../../img/bg.jpg');
	background-position: 50% 50%;
	background-size: cover;
	height: 100vh;
	margin-bottom: 1000px;
	position: relative;
	.header_nav {
		position: fixed;
		top: 0;
		width: 100%;
		background: rgba(0, 0, 0, 0.4);
		border-bottom: 2px solid #ccc;
		z-index: 999;
		.wrapper {
			width: 95%;
			margin: 0 auto;
			display: flex;
			justify-content: space-between;
			align-items: center;
			padding: 20px 0;
			.logo {
				transition: .5s;
				a {
					font-family: 'MS-500';
					color: #ffffff;
					text-decoration: none; }
				&:hover {
					transform: scale(1.1);
					transition: .5s; } }
			.nav {
				nav {
					ul {
						list-style-type: none;
						display: flex; }
					li {
						margin: 0 20px;
						&:last-of-type {
							margin-right: 0; }
