	.header_title {
		width: 100%;
		color: #fff;
		position: absolute;
		top: 40%;
		left: 50%;
		transform: translateX(-50%);
		text-align: center;
		h1 {
			text-transform: uppercase;
			font-family: $msSB;
			font-size: 72px;
			-webkit-text-stroke: 3px #000;
			letter-spacing: 0.075em; }
		.line {
			width: 175px;
			height: 1px;
			background: #ffffff;
			margin: 20px auto 30px auto; }
		p {
			font-size: 18px;
			letter-spacing: 0.075em; } }
	.arrow-down {
		a {
			i {
				color: #ffffff;
				font-size: 40px;
				position: absolute;
				bottom: 10%;
				left: 50%;
				transform: translateX(-50%);
				opacity: .5;
				transition: .5s;
				&:hover {
					opacity: 1;
					transition: .5s; } } } } }
